import React, { useState, useEffect } from 'react';
import { Link, Route, useHistory } from 'react-router-dom';
import './App.css';
import axios from 'axios';
import { API_KEY, API_URL, IMAGE_BASE_URL } from './Config';
import MovieDetail from './Moviedetail';



function App() {
  let history = useHistory();
  
  const [movie, setMovie] = useState([]);
  const [search, setSearch] = useState('');
  const [favMovie, setfavMovie] = useState([]);
  useEffect(() => {

    const endpoint = `${API_URL}movie/popular?api_key=${API_KEY}&language=en-US&page=1`;

    fetch(endpoint)
      .then(response => response.json())
      .then(response => {
        setMovie(response.results)
        console.log(response.results)
       
    })
  }, [])



  const handleChange = e => {
    setSearch(e.target.value)
  }
  const fillteredMovie = movie.filter((movie)=>
    movie.title.toLowerCase().includes(search.toLocaleLowerCase())
    )
  return (
    <div className="movieApp">
      <Route exact path="/">
        <h1 className="title">Current Popular Movies</h1>
        <div className="profile">
            <img src="avata.jpeg" alt=""/>
        </div>
      <div className="movies">
         <form>
          <input type="text"
            placeholder="Search your fav movies!"
            className="movie-input"
            onChange={handleChange}
          />
        </form>
      <div className="movieWrap">
       
          {fillteredMovie.map(function (a,i) {
            return (
              <MovieCard movie={movie} i={i}></MovieCard>
              
            )
          })}
     </div>
        
          <button className="moreBtn" onClick={() => {
            fetch(`${API_URL}movie/popular?api_key=${API_KEY}&language=en-US&page=2`)
              .then(response => response.json())
              .then(response => {
            
             setMovie([...movie,...response.results])
       
    })
  }}>More</button>
        </div>
      </Route>
      <Route exact path="/movie/:movieId" component={MovieDetail}></Route>

     
    </div>
  );
}



function MovieCard(props) {
  let history = useHistory();
  
  
  return (
    <div className="movie-card">
      <div className="movie-img">
        <img src={props.movie[props.i].poster_path?`http://image.tmdb.org/t/p/w500${props.movie[props.i].poster_path}`:null} alt=""/>
      </div>
      <div className="movie-data">
        <h3 className="movie-title">{props.movie[props.i].title }</h3>
        <h5 className="movie-year">{props.movie[props.i].release_date}</h5>
        <p className="movie-genres">{props.movie[props.i].vote_average}</p>
        <p className="movie-summary">{props.movie[props.i].overview.slice(0, 140)}</p>
        <div className="movie-btn">
          <button onClick={()=>{ history.push(`/movie/${props.movie[props.i].id}`) }}>more</button>
      
       </div>
      </div>
    </div>
  )
}


export default App;
